/* frontend/src/styles/orange-theme.css */
/* FORCE LA COULEUR ORANGE PARTOUT */

/* Boutons orange */
.btn-orange {
  background-color: #e38f00 !important;
  border-color: #e38f00 !important;
  color: white !important;
}

.btn-orange:hover {
  background-color: #cc8100 !important;
  border-color: #cc8100 !important;
}

/* Liens orange */
a.text-orange {
  color: #e38f00 !important;
}

a.text-orange:hover {
  color: #cc8100 !important;
}

/* Bordures orange */
.border-orange {
  border-color: #e38f00 !important;
}

/* Backgrounds orange */
.bg-orange {
  background-color: #e38f00 !important;
}

.bg-orange-light {
  background-color: #fef6e6 !important;
}

/* Dégradés orange */
.gradient-orange {
  background: linear-gradient(135deg, #e38f00 0%, #f59e0b 100%) !important;
}

/* Override pour les classes Tailwind */
.bg-primary-500 {
  background-color: #e38f00 !important;
}

.hover\:bg-primary-600:hover {
  background-color: #cc8100 !important;
}

.from-primary-500 {
  --tw-gradient-from: #e38f00 !important;
}

.to-primary-600 {
  --tw-gradient-to: #cc8100 !important;
}